.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  min-width: 216px;
  padding: 11px 31px;

  font-weight: 500;
  font-size: 18px;
  line-height: 1.25;
  color: $color-white;

  background-color: $color-red-500;
  border: 1px solid $color-red-500;
  border-radius: 4px;

  cursor: pointer;

  transition:
    color $trans-default,
    background-color $trans-default,
    border-color $trans-default;

  @include hover-focus {
    background-color: $color-red-400;
    border-color: $color-red-400;
  }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  &--secondary {
    gap: 8px;
    min-width: unset;
    padding: 7px 19px;

    font-weight: 700;
    font-size: 20px;
    line-height: 1.2;

    background-color: $color-green-500;
    border-color: $color-green-500;
    border-radius: 20px;

    @include hover-focus {
      background-color: $color-green-400;
      border-color: $color-green-400;
    }
  }

  &--size-sm {
    min-width: 180px;
  }

  &--light {
    color: $color-red-500;

    background-color: $color-white;

    @include hover-focus {
      color: $color-red-400;

      background-color: $color-white;
      border-color: $color-red-400;
    }
  }

  &--light-dim {
    color: $color-grey-600;

    background-color: $color-white;
    border-color: $color-grey-600;

    @include hover-focus {
      color: $color-red-400;

      background-color: $color-white;
      border-color: $color-red-400;
    }
  }

  &--wide {
    min-width: unset;
    width: 100%;
  }

  &--icon {
    min-width: unset;
    width: 16px;
    height: 16px;
    padding: 0;

    font-weight: 400;
    font-size: 16px;
    color: $color-grey-300;

    background-color: $color-transparent;
    border: none;

    @include hover-focus {
      color: $color-red-400;
      background-color: $color-transparent;
      border-color: $color-transparent;
    }

    &.btn--icon-size-xl {
      width: 32px;
      height: 32px;

      &:not(.btn--no-scale) {
        transition:
          color $trans-default,
          background-color $trans-default,
          border-color $trans-default,
          transform $trans-default;

        @include hover-focus {
          transform: scale(0.9);
        }
      }
    }

    &.btn--icon-size-3xl {
      width: 140px;
      height: 140px;

      transition:
        color $trans-default,
        background-color $trans-default,
        border-color $trans-default,
        transform $trans-default;

      @include hover-focus {
        transform: scale(0.95);
      }
    }
  }

  &--text-control {
    min-width: unset;
    padding: 0;

    font-weight: 400;
    font-size: 16px;
    text-decoration: underline;
    color: $color-grey-400;

    background-color: $color-transparent;
    border: none;

    @include hover-focus {
      color: $color-red-400;
      background-color: $color-transparent;
      border-color: $color-transparent;
    }
  }

  &--add-item {
    min-width: unset;
    width: 100%;
    height: 100%;
    min-height: 360px;
    padding: 11px;

    font-weight: 400;
    font-size: 14px;
    color: $color-grey-600;

    background-color: $color-transparent;
    border-color: $color-grey-150;

    span {
      position: relative;
      display: block;
      padding-top: 50px;

      &::before,
      &::after {
        position: absolute;
        content: '';
        top: 13px;
        left: 50%;
        transform: translateX(-50%);

        width: 30px;
        height: 4px;

        background-color: $color-grey-200;
        border-radius: 1px;

        transition: background-color $trans-default;
      }

      &::after {
        top: 0;
        width: 4px;
        height: 30px;
      }
    }

    @include hover-focus {
      color: $color-red-400;
      background-color: $color-transparent;
      border-color: $color-red-400;

      span {

        &::before,
        &::after {
          background-color: $color-red-400;
        }
      }
    }
  }
}
