.details-box {
  --padding-inline: 20px;

  padding: 25px var(--padding-inline);

  border: 1px solid $color-grey-170;
  border-radius: 4px;

  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.details-box__header {
  display: flex;
  align-items: flex-start;
  gap: 40px;

  &:not(:last-child) {
    margin-bottom: 16px;
  }
}

.details-box__title-wrapper {
  margin-right: auto;
}

.details-box__title {
  display: inline;
  margin: 0;

  font-weight: 500;
  font-size: 22px;
  line-height: 1.2;

  margin-right: auto;

  .details-box__title-wrapper & {
    margin-right: unset;
  }
}

.details-box__hint {
  position: relative;
  display: inline-block;
  transform: translateY(-6px);
  margin-left: 2px;

  @include hover-focus {
    .details-box__hint-content {
      --translate-y: 0;

      visibility: visible;
      opacity: 1;
    }
  }
}

.details-box__hint-icon {
  color: $color-grey-300;

  cursor: pointer;
}

.details-box__hint-content {
  --translate-y: 18px;
  position: absolute;
  left: 50%;
  bottom: calc(100% + 16px);
  transform: translate(-50%, var(--translate-y));

  width: 240px;
  padding: 11px;

  font-size: 14px;

  background-color: $color-white;
  border: 1px solid $color-grey-170;
  border-radius: 8px;

  visibility: hidden;
  opacity: 0;

  transition:
    transform $trans-default,
    visibility $trans-default,
    opacity $trans-default;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 50%) rotate(-45deg);

    width: 12px;
    height: 12px;

    background-color: $color-white;
    border-left: 1px solid $color-grey-170;
    border-bottom: 1px solid $color-grey-170;
  }
}

.details-box__controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 16px 24px;
  min-width: 280px;
  margin: 0;
  padding: 0;

  list-style-type: none;
}
