.header {
  padding: 8px 0;

  color: $color-grey-400;

  background-color: $color-black;
}

.header__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 48px;
}

.header__title {
  flex-shrink: 0;

  font-weight: 400;
  font-size: 16px;
}

.header__title-link {
  span {
    text-decoration: underline;
  }
}

.header__controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 32px;
}

.header__project-select {
  &.ss-main {
    width: unset;
    min-width: 144px;
    padding: 13px 15px;

    color: $color-grey-600;

    border: 1px solid $color-grey-400;
    border-radius: 6px;
  }

  &.ss-content {
    border-radius: 6px;

    .ss-list {
      color: $color-grey-600;

      border: 1px solid $color-grey-400;
      border-radius: 6px;

      .ss-option {
        padding: 7px 15px;

        &:not(.ss-disabled).ss-selected,
        &.ss-highlighted,
        &:hover {
          background-color: $color-red-500;
        }
      }
    }
  }
}

.header__control-list {
  display: flex;
  gap: 32px;
  margin: 0;
  padding: 0;

  list-style-type: none;
}
