.select-input {
  display: block;
}

.select-input__label {
  display: block;
  margin-bottom: 8px;

  font-weight: 500;
  font-size: 16px;
}

.select-input__select {
  &.ss-main {
    gap: 8px;
    width: unset;
    min-width: 144px;
    padding: 12px 11px;

    font-size: 14px;
    line-height: 1.4;
    color: $color-grey-600;

    border: 1px solid $color-grey-150;
    border-radius: 4px;

    .ss-arrow {
      path {
        stroke: $color-red-500;
      }
    }
  }

  &.ss-content {
    border-radius: 4px;

    .ss-list {
      color: $color-grey-600;

      border: 1px solid $color-grey-150;
      border-radius: 6px;

      .ss-option {
        padding: 7px 15px;

        text-overflow: ellipsis;

        &:not(.ss-disabled).ss-selected,
        &.ss-highlighted,
        &:hover {
          background-color: $color-red-500;
        }
      }
    }
  }
}
