.invoice-card {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.invoice-card__pic {
  position: relative;
  width: 100%;
  height: 340px;
}

.invoice-card__pic-link {
  width: 100%;
  height: 100%;
}

.invoice-card__pic-thumb {
  width: 100%;
  height: 100%;

  object-fit: cover;
  object-position: 0 0;
}

.invoice-card__bages {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  max-height: 100%;
  margin: 0;
  padding: 6px 0;

  list-style-type: none;

  overflow: hidden;
  pointer-events: none;
}

.invoice-card__bage {
  padding: 4px 12px;

  font-weight: 500;
  font-size: 12px;
  color: $color-white;

  background-color: $color-red-500;
  border-radius: 12px;
}

.invoice-card__data {
  flex-grow: 1;
  padding: 24px;

  background-color: $color-grey-100;
  border-radius: 0 0 8px 8px;
}

.invoice-card__control-groups {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 12px;
  margin: 0;
  padding: 0;

  list-style-type: none;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.invoice-card__controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 12px;
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.invoce-card__title {
  margin: 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.invoce-card__data-list {
  margin: 0;
  padding: 0;

  list-style-type: none;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.invoce-card__data-item {
  &:not(:last-child) {
    margin-bottom: 12px;
  }
}

.invoce-card__underlined {
  text-decoration: underline;
}

.invoce-card__important {
  color: $color-red-500;
}
