.details-tabs {
  margin-bottom: 40px;
  padding: 40px 0;

  border-top: 1px solid $color-grey-200;
  border-bottom: 1px solid $color-grey-200;
}

.details-tabs__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 16px 24px;
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.details-tabs__tab {
  &.is-active {
    cursor: default;

    @include hover-focus {
      color: $color-white;

      background-color: $color-red-500;
      border-color: $color-red-500;
    }
  }
}
