.text-input {
  display: block;

  &:has(.text-input__btn) {
    position: relative;
  }
}

.text-input__label {
  display: block;
  margin-bottom: 8px;

  font-weight: 500;
  font-size: 16px;
}

.text-input__input {
  width: 100%;
  padding: 9px 11px;

  font-family: inherit;
  font-size: 14px;
  line-height: 1.4;

  border: 1px solid $color-grey-150;
  border-radius: 4px;

  .text-input--size-lg & {
    padding: 11px;
  }

  .text-input:has(.text-input__btn) & {
    padding-right: 39px;
  }
}

.text-input__btn {
  position: absolute;
  right: 12px;
  bottom: 10px;

  color: $color-red-500;

  opacity: 1;

  transition: opacity $trans-default;

  &.is-hiddden {
    opacity: 0;

    pointer-events: none;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);

    width: 20px;
    height: 2px;

    transition: background-color $trans-default;

    background-color: $color-transparent;
  }

  .text-input__input[type="text"] + &::after {
    background-color: $color-red-500;
  }

  @include hover-focus {
    &::after {
      background-color: $color-transparent;
    }

    .text-input__input[type="text"] + &::after {
      background-color: $color-red-400;
    }
  }
}
