.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-black, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal, transform $trans-modal, visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 640px;
    padding: 24px 32px 48px;

    background-color: $color-white;
    border-radius: 4px;

    .modal--size-sm & {
      width: 360px;
      padding: 56px 24px 28px;
    }

    .modal--size-xs & {
      width: 290px;
      padding: 56px 24px 28px;
    }

    &.color-1,
    & .color-1 {
      --color: #{$color-red-550};
    }

    &.color-2,
    & .color-2 {
      --color: #{$color-blue-500};
    }

    &.color-3,
    & .color-3 {
      --color: #{$color-grey-200};
    }
  }

  &__close-btn {
    position: absolute;
    top: 24px;
    right: 32px;
    z-index: 1;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}

.modal__title {
  margin: 0;

  &:not(:last-child) {
    margin-bottom: 40px;
    margin-right: 90px;

    .modal--size-sm &,
    .modal--size-xs & {
      margin-bottom: 24px;
      margin-right: 0;
    }
  }
}

.modal__form {
  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.modal__alone-input {
  &:not(:last-child) {
    margin-bottom: 16px;
  }
}

.modal__inputs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: end;
  gap: 16px 24px;

  &:not(:last-child) {
    margin-bottom: 16px;
  }
}

.modal__file-input {
  gap: 12px;

  .file-input__label {
    font-size: 16px;
  }
}

.modal__form-btns {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 36px 60px;
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.modal__date-pair-inputs {
  display: grid;
  grid-template-columns: 1fr 20px 1fr;
  align-items: center;
  gap: 4px 12px;

  &:not(:last-child) {
    margin-bottom: 16px;
  }
}

.modal__date-pair-legend {
  display: flex;
  gap: 14px;
  grid-column: 1 / -1;

  &::before {
    content: "";

    width: 10px;
    height: 10px;
    margin: 3px 0;

    background-color: var(--color);
    border-radius: 5px;
  }
}

.modal__date-pair-activity {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
}
