.project-tasks {
  margin: 0 calc(-1 * var(--padding-inline));

  overflow-y: auto;

  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.project-tasks__inner {
  padding: 0 var(--padding-inline);
}

.project-tasks__table {
  border-collapse: collapse;
  min-width: 100%;

  th,
  td {
    padding: 14px 16px;

    text-align: center;
    vertical-align: middle;

    border-bottom: 1px solid $color-grey-170;

    &.is-accent {
      color: $color-green-500;
    }

    &.is-important {
      color: $color-red-500;
    }

    &:has(.project-tasks__paid-status) {
      text-align: left;
    }
  }

  th {
    font-weight: 500;
    font-size: 14px;
    line-height: 1.4;
  }

  td {
    font-size: 12px;
    line-height: 1.4;
  }
}

.project-tasks__activity {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
}

.project-tasks__paid-status {
  display: inline-flex;
  gap: 12px;
}

.project-tasks__marker {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-top: 2px;

  border-radius: 5px;
  background-color: $color-green-500;

  &--not-paid {
    background-color: $color-red-500;
  }
}

.project-tasks__link {
  font-weight: 500;
  text-decoration: underline;
  color: $color-red-500;
}

.project-tasks__tag {
  display: inline-flex;
  padding: 3px 15px;

  font-weight: 500;

  background-color: $color-grey-150;
  border: 1px solid $color-grey-150;
  border-radius: 4px;

  &--draft {
    border-color: $color-grey-600;
  }

  &--checking {
    border-color: $color-blue-400;
  }

  &--active {
    border-color: $color-orange-500;
  }

  &--pause {
    border-color: $color-red-500;
  }

  &--done {
    border-color: $color-green-500;
  }
}

.project-tasks__actions {
  display: flex;
  gap: 14px;
}
