.pagination {
  display: flex;
  gap: 22px;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.pagination__link {
  font-size: 14px;
  color: $color-red-500;

  &.is-current {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;

    color: $color-white;

    background-color: $color-red-500;
    border-radius: 50%;
  }
}
