.task-comment__header {
  display: flex;
  justify-content: space-between;
  gap: 16px 40px;
  padding: 10px 12px;

  color: $color-white;

  background-color: $color-grey-200;
  border-radius: 4px;

  .task-comment--accent & {
    background-color: $color-green-500;
  }

  .task-comment--important & {
    background-color: $color-red-500;
  }

  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.task-comment__title {
  margin: 0;

  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
}

.task-comment__time {
  flex-shrink: 0;
  display: flex;
  gap: 16px 24px;
  margin-top: 2px;

  font-size: 12px;
  line-height: 1.33;
}

.task-comment__comment {
  margin: 0;

  font-size: 14px;
  line-height: 1.4;
  color: $color-grey-500;

  &:not(:last-child) {
    margin-bottom: 32px;
  }
}
