// Colors
// ---------------------------------

// Default

$color-transparent: rgba(255, 255, 255, 0);

$color-black: #171717;

$color-grey-600: #333333;
$color-grey-500: #5f5f5f;
$color-grey-400: #858585;
$color-grey-300: #bdbdbd;
$color-grey-200: #dcdcdc;
$color-grey-170: #e8edf3;
$color-grey-150: #ededed;
$color-grey-100: #f9f9f9;

$color-white: #ffffff;


$color-red-700: #842029;
$color-red-550: #bd0a13;
$color-red-500: #cc0000;
$color-red-400: #ed2b1b;
$color-red-100: #f5c2c7;
$color-red-080: #f8d7da;

$color-orange-500: #ed8534;

$color-green-500: #408f1d;
$color-green-400: #3ca012;

$color-blue-500: #0a31bd;
$color-blue-400: #5679d9;

// Gradient

$black-to-right: linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

$ff-primary: "Ubuntu", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-1024: 1024px;
$vp-1280: 1280px;
$vp-1520: 1520px;
