.project-schedule {

  &.color-1,
  & .color-1 {
    --color: #{$color-red-550};
  }

  &.color-2,
  & .color-2 {
    --color: #{$color-blue-500};
  }

  &.color-3,
  & .color-3 {
    --color: #{$color-grey-200};
  }
}

.project-schedule__legend {
  display: flex;
  flex-wrap: wrap;
  gap: 16px 32px;
  margin: 0;
  padding: 0;

  font-size: 12px;
  line-height: 1.4;

  list-style-type: none;

  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.project-schedule__legend-item {
  display: flex;
  gap: 14px;

  &::before {
    content: "";

    width: 10px;
    height: 10px;
    margin: 3px 0;

    background-color: var(--color);
    border-radius: 5px;
  }
}

.project-schedule__schedule {
  margin: 0 calc(-1 * var(--padding-inline));

  overflow-y: auto;
}

.project-schedule__schedule-inner {
  padding: 0 var(--padding-inline);
}

.project-schedule__table {
  border-collapse: collapse;

  tr {
    transition: $trans-default;

    &:has(td) {
      @include hover-focus {
        background-color: $color-grey-170;
      }
    }
  }

  th,
  td {
    min-width: 33px;
    max-width: 33px;
    height: 24px;
    padding: 3px;

    font-weight: 400;
    font-size: 10px;
    line-height: 1.4;
    text-align: center;
    vertical-align: middle;

    border: 1px solid $color-grey-170;

    overflow: hidden;

    &.left {
      min-width: 172px;
      max-width: 172px;

      text-align: left;
    }
  }

  th {
    color: $color-grey-500;

    &.left {
      border: none;
    }
  }

  td {
    cursor: pointer;

    &[class^="color-"] {
      color: $color-white;

      background-color: var(--color);
    }
  }
}
