.file-input {
  display: inline-flex;
  gap: 6px;

  color: $color-red-500;

  transition: color $trans-default;

  cursor: pointer;

  &:has(.file-input__input:hover, .file-input__input:focus) {
    color: $color-red-400;
  }
}

.file-input__icon {
  flex-shrink: 0;
  transform: rotate(180deg);
}

.file-input__label {
  font-weight: 500;
  font-size: 14px;

  .is-hint {
    color: $color-grey-400;
  }

  .is-accent {
    color: $color-green-500;
  }

  .is-important {
    color: $color-red-500;
  }
}
