.details-element__block {
  &:not(:last-child) {
    margin-bottom: 40px;
  }

  &--border-bottom {
    padding-bottom: 40px;
    border-bottom: 1px solid $color-grey-200;
  }
}

.details-element__block-header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 16px 40px;

  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.details-element__block-title {
  margin: 0;

  font-weight: 400;
  font-size: 32px;
  line-height: 1.125;

  &:not(:last-child) {
    margin-bottom: 32px;
  }

  .details-element__block-header > & {
    &:not(:last-child) {
      margin-bottom: 0;
    }
  }
}

.details-element__block-header-link {
  display: inline-flex;
  align-items: center;
  gap: 8px;

  text-decoration: underline;
  color: $color-grey-400;

  &--ml-auto {
    margin-left: auto;
  }

  &--mr-auto {
    margin-right: auto;
  }

  &[data-accordion="button"] {
    .details-element__block.is-active & {
      .text-open {
        display: none;
      }
    }

    .details-element__block:not(.is-active) & {
      .text-close {
        display: none;
      }
    }
  }
}

.details-element__form {
  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.details-element__grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 24px;
  margin: 0;
  padding: 0;

  list-style-type: none;

  &--size-sm {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }

  &--size-xl {
    grid-template-columns: repeat(auto-fill, minmax(500px, 1fr));
  }

  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.details-element__grid-element {
  &--dim {
    padding: 20px 16px;

    background-color: $color-grey-100;
    border-radius: 8px;
  }
}

.details-element__input {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

h3.details-element__grid-element-title {
  margin: 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 1;

  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.details-element__key {
  margin-bottom: 20px;

  font-size: 14px;
  line-height: 1.3;
  color: $color-grey-300;
}

.details-element__value {
  margin: 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
}

.details-element__controls {
  display: flex;
  flex-wrap: wrap;
  gap: 16px 24px;
}

.details-element__control-group {
  display: flex;
  flex-wrap: wrap;
  gap: 16px 24px;
}

.details-element__control {
  &--ml-auto {
    margin-left: auto;
  }

  &--mr-auto {
    margin-right: auto;
  }
}

.details-element__hints {
  width: 100%;
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.details-element__hint {
  display: flex;
  gap: 8px;
  font-weight: 500;

  &:not(:last-child) {
    margin-bottom: 4px;
  }

  &::before {
    content: "";
    display: block;
    width: 8px;
    height: 8px;
    margin-top: 6px;

    background-color: currentColor;
    border-radius: 50%;
  }

  &--accent {
    color: $color-green-400;
  }

  &--important {
    color: $color-red-500;
  }
}

.details-element__document-groups {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.details-element__document-group {
  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.details-element__document-group-title {
  margin: 0;

  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;

  &:not(:last-child) {
    margin-bottom: 24px;
  }
}
