.textarea {
  display: block;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.textarea__label {
  display: block;
  margin-bottom: 8px;

  font-weight: 500;
  font-size: 16px;

  .is-accent {
    color: $color-green-500;
  }

  .is-important {
    color: $color-red-500;
  }
}

.textarea__input {
  width: 100%;
  padding: 9px 11px;

  font-family: inherit;
  font-size: 14px;

  border: 1px solid $color-grey-150;
  border-radius: 4px;
}
