.act-card {
  width: 100%;
  height: 100%;
  padding: 24px;

  background-color: $color-grey-100;
  border-radius: 8px;
}

.act-card__title {
  margin: 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;

  &:not(:last-child) {
    margin-bottom: 4px;
  }
}

.act-card__subtitle {
  margin: 0;

  &:not(:last-child) {
    margin-bottom: 4px;
  }

  &--accent {
    color: $color-green-400;
  }

  &--important {
    color: $color-red-500;
  }
}

.act-card__control-groups {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 12px;
  margin: 0;
  padding: 0;

  list-style-type: none;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.act-card__controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 12px;
  margin: 0;
  padding: 0;

  list-style-type: none;
}
