.contract-card {
  display: flex;
  gap: 50px;
  width: 100%;
  height: 100%;
  padding: 24px 50px;

  background-color: $color-grey-100;
  border-radius: 8px;
}

.contract-card__big-btn {
  margin: 16px 0;
}

.contract-card__header {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.contract-card__title {
  margin: 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;

  &:not(:last-child) {
    margin-bottom: 4px;
  }
}

.contract-card__pages {
  margin: 0;
  padding: 0;

  list-style-type: none;

  &:not(:last-child) {
    margin-bottom: 4px;
  }
}

.contract-card__page {
  display: inline-block;

  &:not(:last-child) {
    margin-right: 4px;
  }
}

.contract-card__page-link {
  text-decoration: underline;
}

.contract-card__status {
  margin: 0;

  font-weight: 500;

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  &--accent {
    color: $color-green-400;
  }

  &--important {
    color: $color-red-500;
  }
}

.contract-card__file-link {
  display: flex;
  gap: 12px;

  font-size: 14px;
  line-height: 1.42;
  text-decoration: underline;

  svg {
    flex-shrink: 0;
  }
}
