.task-details {
  max-width: 940px;
  margin: 0 auto;

  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.task-details__description {
  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.task-details__description-header {
  display: flex;
  justify-content: space-between;
  gap: 16px 40px;

  &:not(:last-child) {
    margin-bottom: 16px;
  }

  &.is-accent {
    color: $color-green-400;
  }

  &.is-important {
    color: $color-red-400;
  }
}

.task-details__description-title {
  margin: 0;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.2;

  &:not(:last-child) {
    margin-bottom: 16px;
  }

  .task-details__description-header & {
    &:not(:last-child) {
      margin-bottom: 0;
    }
  }
}

.task-details__description-controls {
  display: flex;
  flex-wrap: wrap;
  gap: 16px 24px;
  justify-content: flex-end;
  max-width: 320px;
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.task-details__description-content {
  margin: 0;

  &:not(:last-child) {
    margin-bottom: 16px;
  }
}

.task-details__comment-options {
  display: flex;
  flex-wrap: wrap;
  gap: 16px 24px;
  justify-content: flex-end;
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.task-details__comment-controls {
  display: flex;
  gap: 16px 40px;
  justify-content: space-between;
}

.task-details__comments {
  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.task-details__comments-title {
  margin: 0;

  font-weight: 500;
  font-size: 22px;
  line-height: 1;

  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.task-details__comment-list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.task-details__comment-item {
  &:not(:last-child) {
    margin-bottom: 32px;
  }
}
