.summary__sections {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(420px, 1fr));
  gap: 32px 24px;
  padding: 0;

  list-style-type: none;

  .is-accent {
    color: $color-green-400;
  }

  a {
    color: $color-red-500;
  }
}

.summary__section {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.summary__section-title {
  margin: 0;

  font-weight: 500;
  font-size: 20px;
}

.summary__list {
  flex-grow: 1;
  margin: 0;
  padding: 28px;

  background-color: $color-grey-100;

  list-style-type: none;
}

.summary__list-item {
  &:not(:last-child) {
    margin-bottom: 16px;
  }
}
