.message {
  width: 100%;
  margin: 0;
  padding: 16px;

  color: $color-grey-600;

  background-color: $color-grey-100;
  border: 1px solid $color-grey-200;
  border-radius: 4px;

  strong,
  b {
    font-weight: 700;
    text-transform: uppercase;
  }

  &--important {
    color: $color-red-700;

    background-color: $color-red-080;
    border-color: $color-red-100;
  }
}
