.extended-input {
  display: flex;
  align-items: center;
  gap: 8px;
}

.extended-input__label {
  flex-shrink: 0;
  margin: 0;

  color: $color-grey-400;
}

.extended-input__value {
  margin: 0;

  font-weight: 500;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.extended-input__select {
  &.ss-main {
    gap: 8px;
    width: unset;
    min-width: 144px;
    min-height: unset;
    padding: 0;

    color: $color-grey-600;
    background-color: $color-transparent;

    border: none;

    .ss-values {
      .ss-single {
        margin: 0;
      }
    }

    .ss-arrow {
      margin-top: 4px;
      margin-bottom: 4px;

      path {
        stroke: $color-red-500;
      }
    }
  }

  &.ss-content {
    border: none;
    border-radius: 2px;

    .ss-list {
      color: $color-grey-600;

      border: 1px solid $color-grey-300;
      border-radius: 2px;

      .ss-option {
        padding: 0;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        &:not(.ss-disabled).ss-selected,
        &.ss-highlighted,
        &:hover {
          background-color: $color-red-500;
        }
      }
    }
  }
}

.extended-input__icon {
  flex-shrink: 0;
}
